body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: auto;
}
.nav {
    height: 7vh; 
    display: -webkit-flex; 
    display: flex;
    list-style: none;
    margin: 0;
    font-size: 1.2em;    
    min-height: 60px;
    color:#FFF;
    border-radius:4px;
    border:1px solid #bbb;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
}

.iconpref {
    width: 10%;
}

@media only screen and (max-width: 700px) {
    .nav {
        font-size: .75em;
        padding: 0;
    }
    .noflex {
        display:none;
    }
}
.push {
    margin-left: auto;
}

li {
    padding: 20px;    
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
  display: none;
  margin-top: 5px;
  border-radius: 10px;
  list-style: none;
  background-color: rgba(0,0,0,.7);
  position: absolute;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
  -webkit-animation-name: example;
  -webkit-animation-duration: 1s;
  animation-name: example;
  animation-duration: 1s;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes example {
  from {background-color: rgba(50, 50, 50,.9);}
  to {background-color: rgb(0,0,0,.9);}
}

/* Standard syntax */
@keyframes example {
  from {background-color: rgba(50,50,50,.9);}
  to {background-color: rgb(0,0,0,.7);}
}

.dropdown:hover .dropdown-content {
  display: block;
}

.main {
    min-height: 92.8vh;
}
.bgblue {
    background: #363377; /* Old browsers */ /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#363377), color-stop(44%,#8180B2), color-stop(100%,#1B1A59)); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, #363377 0%,#8180B2 44%,#1B1A59 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
    background: -webkit-linear-gradient(right, #363377 0%,#8180B2 44%,#1B1A59 100%);
    background: linear-gradient(to left, #363377 0%,#8180B2 44%,#1B1A59 100%); /* W3C */
}
