    background: -webkit-linear-gradient(top, #8B2E68 0%,#AA5089 44%,#4D0131 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
    background: -webkit-linear-gradient(right, #8B2E68 0%,#AA5089 44%,#4D0131 100%);
